html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: #e0e9f5;
}

main {
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}

p {
  margin: 0
}

body {
    text-align: center;
    background: linear-gradient(170deg, #061a2c, #2a2727);
    height: 100vh;
}

.content {
    background-color: #061a2c;
    padding-left: 7vw;
    padding-right: 7vw;
    text-align: left;
    font-size: calc(10px + 2vmin);
}

.grid-container {
    display: grid;
    grid: auto / 55vw ;
    grid-gap: 10px;
    padding: 10px;
    overflow: hidden;
}

.content h1, h5 {
    color: #ff9900;
}
.content h5 {
    color: #ceb471;
    font-weight: bold;
    border-bottom: 2px solid #ff920f;
}

.content p {
    font-size: 19px;
    color: #ceb471;
}


.Quote_container__3Blso {
    color: rgba(197, 160, 124, 0.93);
    text-align: left;
}

.Quote_container__3Blso p{

    background: -webkit-linear-gradient(80deg, #f6b455 0%, #eee 85%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

p.Quote_quote__2eCbw  {
    font-style: italic;
    font-size: 20px;
}

p.Quote_author__3dv_C {
    font-size: 20px;

}

.MainHeader_extraSpace__2zmlQ {
    width: 100vw;
    min-width: 4px;
    min-height: 70px;
}

.MainHeader_context__22ZEE {
    width: 100vw;
    height: 400px;
    background: linear-gradient(170deg, #061a2c, #282f31);
}

.MainHeader_context__22ZEE nav {
    display: flex;
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    z-index: 1;
    height: 70px;
    list-style: none;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-content: flex-start;
}

.MainHeader_signature__1xpUc h5 {
    color: #ff9900;
    font-size: 1.5rem;
    font-family: "Menlo";
    padding-left: 5vw;
    margin-right: 100px;
}

.MainHeader_context__22ZEE ul {
    list-style: none;
    display: flex;
}

.MainHeader_context__22ZEE li {
    margin: 0 1rem;
    width: 5rem;
}

.MainHeader_context__22ZEE a {
    color: ghostwhite;
    text-decoration: none;
}

.MainHeader_context__22ZEE a:active,
.MainHeader_context__22ZEE a.MainHeader_active__11wpQ {
    padding-bottom: 0.25rem;
    border-bottom: 2px solid #ff9900;
}

.MainHeader_context__22ZEE a:hover {
    color: #ff9900;
}

img.MainHeader_fadeSection__3a5Rk {
    z-index: 2;
    width: 100vw;
    height: 400px;
    fill: #061a2c;

    position: absolute;
    left: 0;
    top: 150px;
}

div.MainHeader_temporaryLogo__3VEh_ {
    z-index: 10;
    position: absolute;
    right: 40px;
    top: 220px;
    width: 150px;
    height: 100px;
    color: red;
}

div.MainHeader_temporaryLogo__3VEh_ p {
    padding-top: 20px;
    color: rgba(196, 197, 201, 0.47);
}


div.MainHeader_temporaryLogo__3VEh_ svg {
    pointer-events: none;
    fill: #c4c5c9;
}

@media (prefers-reduced-motion: no-preference) {
    div.MainHeader_temporaryLogo__3VEh_ svg {
        animation: MainHeader_gear-spin__37Dyh infinite 5s cubic-bezier(0.465, 0.183, 0.153, 0.946);
    }
}

@media (prefers-reduced-motion: no-preference) {
    div.MainHeader_temporaryLogo__3VEh_ p {
        animation: MainHeader_text-shake__1ckRB infinite 3s ease-in-out;
    }
}



@keyframes MainHeader_text-shake__1ckRB {
    0% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(10deg);
    }
    50% {
        transform: rotate(0deg);
    }
    75% {
        transform: rotate(-10deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@keyframes MainHeader_gear-spin__37Dyh {
    0% {
        transform: rotate(0deg);
    }
    16% {
        transform: rotate(60deg);
    }
    32% {
        transform: rotate(120deg);
    }
    49% {
        transform: rotate(180deg);
    }
    66% {
        transform: rotate(240deg);
    }
    82% {
        transform: rotate(300deg);
    }
    100% {
        transform: rotate(360deg);
    }
}





